.network{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
    padding-left: 1rem;
    padding-right: 1rem;
}
.boton{
    flex: 0 0 50%;
    max-width: 50%;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
}
.bton-network{
    text-align: center;
    color: #FFFFFF;
    padding-left: 50px;
    overflow: hidden;
    position: relative;
    padding: 12px 20px;
    font-size: 1rem;
    display: block;
    font-weight: 900;
    border-radius: 8px;
    text-transform: uppercase;
    text-align: center;
    cursor: pointer;
    user-select: none;
}
.facebook{
    background-color: #3b5998;
}

.bton-network span{
    margin-left: 3rem;
    font-family: $fuente_principal;
}

.bton-network i{
    position: absolute;
    left: 0;
    top: 0;
    line-height: 43px;
    width: 40px;
    height: 100%;
    background-color: rgba(0,0,0,.1);
}

.whatsapp{
    background-color: #34AF23;
}

.boton-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem 2rem 7rem 2rem;
}

.boton-main a{
    background-color: #0034c8;
    color: #FFFFFF;
    width: 100%;
    border-radius: 5px;
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-transform: uppercase;
    font-family: $fuente_principal;
    font-size: 12px;
    font-weight: $bold;
    margin-bottom: 2rem;
}