.contact{
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 7rem;
    position: absolute;
    top: 15rem;
    left: 0;
    right: 0;
}

.card-contact{
    background-color: $blanco;
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    display: block;
    margin-bottom: 3rem;
}

.card-contact h3{
    font-family: $fuente_secundaria;
    font-weight: $bold;
    font-size: 18px;
    color: #358AE7;
    display: inline-block;
    margin: 0;
}

.card-contact hr{
    color:rgba(0,0,0,.05);
}

.info-card h4{
    color: #ababab;
    font-weight: $regular;
    font-family: $fuente_principal;
    font-size: 10px;
    margin: 0;
}

.info-card a{
    color: #000;
    font-weight: $regular;
    font-size: 14px;
    opacity: .7;
    text-decoration: none;
    display: flex;
    justify-content: space-between;
    font-family: $fuente_principal;
}
.info-card i{
    color: $azul;
}
