.card-maps{
    padding: 0;
    border-radius: 1rem;
    overflow: hidden;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    width: 95%;
    margin-left: auto;
    margin-right: auto;
}
.card-maps iframe{
    width: 100%;
    height: 47rem;
}