.second{
    background-image: url('/build/img/14m.webp');
}

.third{
    background-image: url('/build/img/17m.webp');
}

.first{
    background-image: url('/build/img/8m.webp');
}
.card{
    position: relative;
    overflow: hidden;
    background-size: cover;
    background-position: center center;
    height: 30rem;
    width: 85%;
    border-radius: 3rem;
    display: block;
    margin-left: auto;
    margin-right: auto;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.bg-card{
    background: linear-gradient(to bottom,rgba(240,240,240,0) 0%,rgba(240,240,240,0) 30%,rgba(240,240,240,0.3) 40%,rgba(240,240,240,0.5) 50%,#f0f0f0 80%,rgba(247,247,247,0.95) 100%);
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: absolute;
    z-index: 1;
}
.content-card{
    position: relative;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 10;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-left: 2rem;
    padding-right: 2rem;
}
.content-card h1{
    font-size: 24px !important;
    font-weight: $bold;
    margin: 0;
    margin-bottom: 5px;
}
.content-card p{
    text-align: center;
    margin-top: 0;
    margin-bottom: 30px;
    word-wrap: break-word;
    font-size: 13px;
}
.header-bottom{
    margin-top: -18rem;
}
.slider{
    width: 100%;
    height: 35rem;
    margin: auto;
    overflow: hidden;
    position: relative;
    left: 0;
    right: 0;
}
.slider-card{
    display: flex;
    padding: 0;
    width: 300%;

    animation: slide 20s infinite alternate;
}
.slider-element{
    width: 100vw;
    display: flex;
    align-content: center;
    justify-content: center;
}
@keyframes slide {
    0%{margin-left: 0;}
    30%{margin-left: 0;}

    35%{margin-left: -100%;}
    65%{margin-left: -100%;}
    
    70%{margin-left: -200%;}
    100%{margin-left: -200%;}
}
