/** Media Queries **/
.active {
  background-color: #0034c8;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 5px;
}
.active:hover {
  background-color: #002ba4;
  cursor: pointer;
}

.inactive {
  background-color: #616161;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
  border-radius: 5px;
}
.inactive:hover {
  background-color: #4f4f4f;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #0034c8;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
  border-radius: 5px;
}
.boton-azul-block:hover {
  background-color: #002ba4;
  cursor: pointer;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  min-height: 550px;
  background-color: #f0f0f0;
  overflow-x: hidden;
}

.contenedor {
  width: 95%;
  max-width: 160rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 95%;
}

h1, h2, h3 {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 24px;
}

p {
  font-family: "Roboto", sans-serif;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.background {
  background-image: url("/build/img/20.webp");
  background-size: cover;
  background-position: center center;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 310px;
  background-size: cover;
  z-index: 0;
  border-bottom-left-radius: 45px;
  border-bottom-right-radius: 45px;
}

.mask {
  background-color: #0034c8;
  background-position: center center;
  background-size: cover;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: 1;
  opacity: 0.9;
  border-bottom-left-radius: 45px;
  border-bottom-right-radius: 45px;
}

.logo {
  width: 200px;
  margin: 50px auto 50px auto;
  display: table;
}

.second {
  background-image: url("/build/img/14m.webp");
}

.third {
  background-image: url("/build/img/17m.webp");
}

.first {
  background-image: url("/build/img/8m.webp");
}

.card {
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
  height: 30rem;
  width: 85%;
  border-radius: 3rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.bg-card {
  background: linear-gradient(to bottom, rgba(240, 240, 240, 0) 0%, rgba(240, 240, 240, 0) 30%, rgba(240, 240, 240, 0.3) 40%, rgba(240, 240, 240, 0.5) 50%, #f0f0f0 80%, rgba(247, 247, 247, 0.95) 100%);
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  position: absolute;
  z-index: 1;
}

.content-card {
  position: relative;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-left: 2rem;
  padding-right: 2rem;
}

.content-card h1 {
  font-size: 24px !important;
  font-weight: 700;
  margin: 0;
  margin-bottom: 5px;
}

.content-card p {
  text-align: center;
  margin-top: 0;
  margin-bottom: 30px;
  word-wrap: break-word;
  font-size: 13px;
}

.header-bottom {
  margin-top: -18rem;
}

.slider {
  width: 100%;
  height: 35rem;
  margin: auto;
  overflow: hidden;
  position: relative;
  left: 0;
  right: 0;
}

.slider-card {
  display: flex;
  padding: 0;
  width: 300%;
  animation: slide 20s infinite alternate;
}

.slider-element {
  width: 100vw;
  display: flex;
  align-content: center;
  justify-content: center;
}

@keyframes slide {
  0% {
    margin-left: 0;
  }
  30% {
    margin-left: 0;
  }
  35% {
    margin-left: -100%;
  }
  65% {
    margin-left: -100%;
  }
  70% {
    margin-left: -200%;
  }
  100% {
    margin-left: -200%;
  }
}
.network {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
  padding-left: 1rem;
  padding-right: 1rem;
}

.boton {
  flex: 0 0 50%;
  max-width: 50%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}

.bton-network {
  text-align: center;
  color: #FFFFFF;
  padding-left: 50px;
  overflow: hidden;
  position: relative;
  padding: 12px 20px;
  font-size: 1rem;
  display: block;
  font-weight: 900;
  border-radius: 8px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  user-select: none;
}

.facebook {
  background-color: #3b5998;
}

.bton-network span {
  margin-left: 3rem;
  font-family: "Roboto", sans-serif;
}

.bton-network i {
  position: absolute;
  left: 0;
  top: 0;
  line-height: 43px;
  width: 40px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
}

.whatsapp {
  background-color: #34AF23;
}

.boton-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem 7rem 2rem;
}

.boton-main a {
  background-color: #0034c8;
  color: #FFFFFF;
  width: 100%;
  border-radius: 5px;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  font-weight: 700;
  margin-bottom: 2rem;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 25;
  background-color: rgba(249, 249, 249, 0.98);
  box-shadow: 0 -5px 10px 0 rgba(0, 0, 0, 0.06);
  min-height: 60px;
  width: 100%;
  display: flex;
  text-align: center;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

footer a {
  color: #1f1f1f;
  padding-top: 12px;
  position: relative;
  flex: 1 1 auto;
}

.footer a span {
  position: relative;
  z-index: 2;
  display: block;
  font-size: 10px;
  font-weight: 500;
  margin-top: -6px;
  opacity: 0.7;
  font-family: "Roboto", sans-serif;
}

.footer a i {
  color: #0034c8;
}

.service_one {
  background-image: url("/build/img/services/1.webp");
}

.service_two {
  background-image: url("/build/img/services/2.webp");
}

.service_three {
  background-image: url("/build/img/services/3.webp");
}

.service_four {
  background-image: url("/build/img/services/4.webp");
}

.service_five {
  background-image: url("/build/img/services/5.webp");
}

.service_six {
  background-image: url("/build/img/services/6.webp");
}

.service_seven {
  background-image: url("/build/img/services/7.webp");
}

.card-gallery {
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 1rem;
  width: 100%;
  height: 30rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.gallery {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 2rem;
  padding-right: 2rem;
}

.mask-gallery {
  padding: 1rem;
  height: 100%;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0.1) 30%, rgba(0, 0, 0, 0.8) 70%, #000 100%);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  color: white;
}

.mask-gallery h2 {
  font-size: 16px;
}

.section-service {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  justify-content: center;
}

.card-gallery-grid {
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 1rem;
  width: 100%;
  height: 20rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.contact {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 7rem;
  position: absolute;
  top: 15rem;
  left: 0;
  right: 0;
}

.card-contact {
  background-color: #FFFFFF;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  display: block;
  margin-bottom: 3rem;
}

.card-contact h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 18px;
  color: #358AE7;
  display: inline-block;
  margin: 0;
}

.card-contact hr {
  color: rgba(0, 0, 0, 0.05);
}

.info-card h4 {
  color: #ababab;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  font-size: 10px;
  margin: 0;
}

.info-card a {
  color: #000;
  font-weight: 400;
  font-size: 14px;
  opacity: 0.7;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  font-family: "Roboto", sans-serif;
}

.info-card i {
  color: #0034c8;
}

