.card-maps {
  padding: 0;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}

.card-maps iframe {
  width: 100%;
  height: 47rem;
}

.sharing-card {
  position: fixed;
  z-index: 15;
  top: 0;
  left: 0;
  bottom: 0px;
  right: 0;
  background-color: #000;
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
}

.card-share {
  background-color: #FFFFFF;
  position: fixed;
  bottom: -150rem;
  left: 0;
  right: 0;
  z-index: 20;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 1rem;
  padding: 1rem;
  transition: bottom 0.5s ease;
}

.title-share h4 {
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  font-weight: 400;
  margin: 0;
  text-align: center;
}

.title-share p {
  color: #6c6c6c;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  text-align: center;
  margin: 0;
}

.grid-share {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 10rem);
}

.qr-share img {
  height: auto;
  width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.btn-share {
  text-align: center;
}

.btn-share i {
  width: 5rem;
  height: 5rem;
  color: #fff;
  margin-bottom: 1rem;
  border-radius: 1rem;
  text-align: center;
  line-height: 3;
}

.btn-share p {
  font-size: 11px;
  color: #6c6c6c;
  margin: 0;
}

.footer-share p {
  font-size: 10px;
  text-align: center;
  color: #6c6c6c;
  font-family: "Roboto", sans-serif;
  margin: 0;
}

.btn-share .fa-facebook-f {
  background-color: #263A63;
}

.btn-share .fa-twitter, .btn-share .fa-envelope, .btn-share .fa-telegram-plane {
  background-color: #4A96E9;
}

.btn-share .fa-whatsapp {
  background-color: #227217;
}

.btn-share .fa-times {
  background-color: #b60000;
}

.overlay {
  position: fixed;
  top: 20rem;
  bottom: 0;
  left: 1rem;
  right: 1rem;
  border-radius: 1rem;
  z-index: 35;
}

/* Card Install */
.card-pwa {
  background-color: #FFFFFF;
  position: fixed;
  left: 0;
  right: 0;
  top: -40rem;
  z-index: 20;
  width: 95%;
  height: 37rem;
  margin-left: auto;
  margin-right: auto;
  border-radius: 1rem;
  padding: 3rem 1rem;
  transition: top 0.5s ease;
}

.card-pwa img {
  width: 9rem;
  height: 9rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.card-pwa h2 {
  font-size: 18px;
  margin: 0;
  font-weight: 700;
}

.card-pwa p {
  font-size: 13px;
  color: #6c6c6c;
  margin: 3rem 0;
  text-align: center;
}

.card-pwa a, .card-pwa button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.card-pwa a {
  width: 70%;
  background-color: #0034c8;
  border-radius: 1rem;
  padding: 1rem;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  margin-bottom: 3rem;
}

.card-pwa button {
  border: none;
  background-color: #FFFFFF;
  color: #ccd1d9;
  font-size: 15px;
  opacity: 0.6;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
}

/* Iphone */
.card-pwa-iphone {
  background-color: #FFFFFF;
  position: fixed;
  left: 0;
  right: 0;
  top: -40rem;
  z-index: 20;
  width: 95%;
  height: 37rem;
  margin-left: auto;
  margin-right: auto;
  border-radius: 1rem;
  padding: 3rem 1rem;
  transition: top 0.5s ease;
}

.card-pwa-iphone > img {
  width: 9rem;
  height: 9rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.share-icon-iphone {
  width: 1.5rem;
  height: 2rem;
  display: inline-block;
  margin: 0 0.5rem;
}

.card-pwa-iphone h2 {
  font-size: 18px;
  margin: 0;
  font-weight: 700;
}

.card-pwa-iphone p {
  font-size: 13px;
  color: #6c6c6c;
  margin: 3rem 0;
  text-align: center;
}

.card-pwa-iphone a, .card-pwa-iphone button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.card-pwa-iphone a {
  width: 70%;
  color: #ccd1d9;
  text-transform: uppercase;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
}